.features-container_feature {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-direction: row;
    margin: 1rem;
}

.features-container_feature-title {
    flex: 1;
    max-width: 180px;
    margin-right: 2rem;
}

.features-container_feature-title h1 {
    font-family: var(--font-family);
    font-size: 20px;
    font-weight: 900;
    line-height: 30px;
    line-height: 24px;
    letter-spacing: -0.04em;
    color: white;
}

.features-container_feature-title div {
    width: 38px;
    height: 4px;
    background: var(--gradient-bar);
    box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
    margin-bottom: 0.25rem ;
}

.features-container_feature-text {
    flex: 2;
    max-width: 390px;
    display: flex;
}

.features-container_feature-text p {
    font-family: var(--font-family);
    color: #8cd6f6;
    font-weight: 200;
}

@media screen and (max-width: 550px) {
    .features-container_feature-title h1 {
        font-size: 14px;
        line-height: 20px;
    }

    .features-container_feature {
        margin: 1rem 0;
    }
}